import uCharts from '@/components/u-charts/u-charts.js';
var _self;
var canvaArcbar1;

export default {
	data() {
		return {
			cWidth: '',
			cHeight: '',
			cWidth3: '', //圆弧进度图
			cHeight3: '', //圆弧进度图
			arcbarWidth: '', //圆弧进度图，进度条宽度,此设置可使各端宽度一致
			pixelRatio: 1,
			detail: {
				"stuCorrectAccuracy": "50.00%",
				"defeatStuCount": "33.33%",
				"joinStudentCount": 3,
				"averageCorrectAccuracy": "33.33%",
				"bestCorrectAccuracy": "50.00%",
				"answerQuestionCount": 1,
				"sendQuestionCount": 2,
				"answerTrueQuestionCount": 1,
				"classRank": 1,
				"totalScore": 13,
				"studentAnalyseDetail": {
					"stuCode": "25dacf6dc11c482587ad84c84ceb9ad7",
					"stuName": "明明",
					"classRank": 1,
					"className": null,
					"accuracy": "50.00%",
					"totalScore": 13,
					"gradeRank": null,
					"answeredQuestionCount": 1,
					"answerTrueQuestionCount": 1,
					"questionList": [{
							"questionName": "单题单选",
							"questionId": "1",
							"questionType": "1",
							"trueAnswer": "A",
							"inputAnswer": "A",
							"answerResult": "true",
							"answerDatetime": "2020-04-02 20:21:45",
							"speed": "1.76",
							"score": 13,
							"voiceUrl": null
						},
						{
							"questionName": "单题多选",
							"questionId": "2",
							"questionType": "2",
							"trueAnswer": "ABC",
							"inputAnswer": "",
							"answerResult": "false",
							"answerDatetime": "2020-04-16 10:37:28",
							"speed": "0",
							"score": 0,
							"voiceUrl": null
						}
					]
				},
				"first10StudentList": [{
						"stuCode": "25dacf6dc11c482587ad84c84ceb9ad7",
						"stuName": "明明",
						"classRank": 1,
						"className": null,
						"accuracy": "50.00%",
						"totalScore": 13
					},
					{
						"stuCode": "a09ef1b5755a4f3e8439313073d577e6",
						"stuName": "香香",
						"classRank": 2,
						"className": null,
						"accuracy": "50.00%",
						"totalScore": 12
					},
					{
						"stuCode": "a7467c41d95b470781f7896b1a8eabca",
						"stuName": "一一",
						"classRank": 3,
						"className": null,
						"accuracy": "0.00%",
						"totalScore": 0
					}
				]
			},
		}
	},
	onLoad() {
		_self = this;











		this.cWidth3 = uni.upx2px(250); //这里要与样式的宽高对应
		this.cHeight3 = uni.upx2px(250); //这里要与样式的宽高对应
		this.arcbarWidth = uni.upx2px(34);
		this.cWidth = uni.upx2px(700); //这里要与样式的宽高对应
		this.cHeight = uni.upx2px(500); //这里要与样式的宽高对应

		this.getServerData();
	},
	methods: {
		getstudentAnalyseDetail() {
			postajax(api.studentAnalyseDetail, {
				"topicCode": "463f8e6d3c814958ba3a720c72066550",
				"stuCode": "25dacf6dc11c482587ad84c84ceb9ad7"
			}).then(da => {
				{
					console.log(da)
					if (da.code == 0) {
						this.studentAnalyseDetail = da.data;
					}
				}
			})
		},
		getServerData() {
			let Arcbar2 = {
				series: []
			};
			let Arcbar3 = {
				series: []
			};
			//这里我后台返回的是数组，所以用等于，如果您后台返回的是单条数据，需要push进去
			Arcbar2.series = [{
				name: '正确率',
				"data": this.detail.stuCorrectAccuracy.slice(0,this.detail.stuCorrectAccuracy.length-1)/100,
				"color": "#81a3e2"
			}];
			Arcbar3.series = [{
				name: '击败人数比',
				"data": this.detail.defeatStuCount.slice(0,this.detail.defeatStuCount.length-1)/100,
				"color": "#99c2cc"
			}];
			_self.showArcbar2("canvasArcbar2", Arcbar2);
			_self.showArcbar3("canvasArcbar3", Arcbar3);

		},

		showArcbar2(canvasId, chartData) {
			new uCharts({
				$this: _self,
				canvasId: canvasId,
				type: 'arcbar',
				fontSize: 11,
				legend: false,
				title: {
					name: Math.round(chartData.series[0].data * 100) + '%',
					color: chartData.series[0].color,
					fontSize: 25 * _self.pixelRatio
				},
				subtitle: {
					name: chartData.series[0].name,
					color: '#666666',
					fontSize: 12 * _self.pixelRatio
				},
				extra: {
					arcbar: {
						type: 'circle',
						width: _self.arcbarWidth * _self.pixelRatio, //圆弧的宽度
						backgroundColor: '#e28193',
						startAngle: 1.25
					}
				},
				background: '#FFFFFF',
				pixelRatio: _self.pixelRatio,
				series: chartData.series,
				animation: true,
				width: _self.cWidth3 * _self.pixelRatio,
				height: _self.cHeight3 * _self.pixelRatio,
				dataLabel: true,
			});

		},
		showArcbar3(canvasId, chartData) {
			new uCharts({
				$this: _self,
				canvasId: canvasId,
				type: 'arcbar',
				fontSize: 11,
				legend: false,
				title: {
					name: Math.round(chartData.series[0].data * 100) + '%',
					color: chartData.series[0].color,
					fontSize: 25 * _self.pixelRatio
				},
				subtitle: {
					name: chartData.series[0].name,
					color: '#333',
					fontSize: 12 * _self.pixelRatio
				},
				extra: {
					arcbar: {
						type: 'circle', //整圆类型进度条图
						width: _self.arcbarWidth * _self.pixelRatio, //圆弧的宽度
						backgroundColor: '#e281b6',
						startAngle: 1.25,
					}
				},
				background: '#FFFFFF',
				pixelRatio: _self.pixelRatio,
				series: chartData.series,
				animation: true,
				width: _self.cWidth3 * _self.pixelRatio,
				height: _self.cHeight3 * _self.pixelRatio,
				dataLabel: true,
			});

		}
	}
}
